// Header.tsx
'use client'

import React, { useEffect, useRef, useState } from 'react'
import { useRouter } from 'next/router'
import { useTheme } from 'next-themes'
import Link from 'next/link'
import { Input } from '@/components/ui/input'
import { Button } from '@/components/ui/button'
import { Menu, ShoppingCart, Sun, Moon } from 'lucide-react'
import { AuthControls } from '@/components/AuthControls'
import { useDebounce } from '@/hooks/useDebounce'

export function Header({ toggleSidebar }: { toggleSidebar: () => void }) {
  const router = useRouter()
  const { theme, setTheme } = useTheme()
  const [mounted, setMounted] = useState(false)

  // Only trigger redirect once user has actually typed
  const didSearch = useRef(false)

  // Search term state
  const [term, setTerm] = useState('')

  useEffect(() => {
    setMounted(true)
  }, [])

  // Debounce navigation, but only if didSearch is true
  useDebounce(term, 500, () => {
    if (!didSearch.current) return
    router.push({
      pathname: '/listings',
      query: term ? { search: term } : {},
    })
  })

  return (
    <header className="fixed top-0 inset-x-0 z-50 h-16 bg-background border-b flex items-center px-4">
      {/* Hamburger & Logo omitted for brevity */}

      <div className="flex-1">
        <Input
          placeholder="Search outfits..."
          value={term}
          onChange={e => {
            didSearch.current = true      // mark that user has typed
            setTerm(e.target.value)
          }}
        />
      </div>

      {/* Theme toggle, cart, auth controls omitted */}
    </header>
  )
}
